import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { subscribe } from '../actions/newsletterActions'

export default function Footer() {
    const dispatch = useDispatch()
    const [newsletterMessage, setNewsletterMessage] = useState('');
    const handleNewsletterSubmit = (e) => {
        e.preventDefault()
        subscribe(e.target.email.value)
        // let email = await dispatch(subscribe(e.target.email.value))
        // email.data.error ? setNewsletterMessage('Invalid email!') : setNewsletterMessage(`${e.target.email.value} added to newsletter`) 
    }

    return (
        <section >
            <div className='container-fluid'>
                <div className='row d-flex'>
                    <div className='col col-left justify-content-center justify-content-sm-start'>
                        <p>About</p>
                        <p>Privacy</p>
                        <p>Shipping</p>
                        <p>Policy</p>
                    </div>
                    <div className='col-8 col-right text-right d-none d-md-flex justify-content-sm-end'>
                        <div>
                            <form onSubmit={handleNewsletterSubmit}>
                                <p>Newsletter Sign-Up</p>
                                <input type='email' name='email' placeholder='Email'/>
                                <input type='submit' value='Submit' className='btn btn-outline-secondary btn-sm'/>
                            </form>
                            {newsletterMessage && (<p>{newsletterMessage}</p>) }
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
